<template>
  <div ref="scrollWarpper" class="scroll-warpper">
    <slot></slot>
  </div>
</template>

<script>
  import BScroll from "better-scroll";
  export default {
    name: "scroll",
    props: {
      data: {
        type: Array,

      },
    },
    data() {
      return {
        scroll: null,
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initScroll();

        //  this.scroll.on('pullingUp', () => {
        //   console.log('')
        //   setTimeout(() => {
        //     //  better-scroll 
        //     this.scroll.finishPullUp()
        //   }, 2000)
        // })
      });
    },
    watch: {
      data() {
        this.$nextTick(() => {
          this.scroll.refresh();
          //
          this.scroll.finishPullUp();
        });
      },
    },
    methods: {
      //
      initScroll() {
        if (!this.$refs.scrollWarpper) return;
        this.scroll = new BScroll(this.$refs.scrollWarpper, {
          pullUpLoad: {
            threshold: 100,
            probeType: 3,
            startY: 0,
          },
          click: true,
          taps: true,
        });
      },
      //
      refresh() {
        this.$refs.scrollWarpper && this.scroll.refresh();
      },
      scrollToElement() {
        console.log('sdd');
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      },
      scrollTo() {
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
    },
  };
</script>

<style></style>
