import request,{spareRequest} from "./request";
import axios from "axios";
//
export const getSwiper = () => {
    return request({
        method: 'GET',
        url: '/banner'
    })
}
//icons

export const getIcons = () => {
    return axios({
        method: 'GET',
        url: 'https://autumnfish.cn/homepage/dragon/ball'
    })
}


